@import url('https://fonts.googleapis.com/css?family=Lateef');

:root {
    ---eigengrau: #16161D;
    ---body_fonts: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    ---title_fonts: Garamond, serif;
}

body {
    background-color: var(---eigengrau);
    font-family: var(---body_fonts)
}

header {
    width: 40%;
    min-width: 375px;
    margin: 0px 0px 0px 1%;
}

header fieldset {
    border: 4px solid #fff;
    box-shadow: 20px 20px 0px 10px rgba(0,0,0,1);
}

header legend {
    font-family: var(---title_fonts);
    font-size: calc(20px + 3vh);
    color: #fff;
}

header ul {
    list-style-type: none;
    background-color: rgba(255,255,255,0.05);
    padding: 0px;
    border-radius: 20px;
}

header li {
    border-radius: 20px;
}

header li:hover {
    border: 2px solid white;
    background-color: rgba(0,0,0,0.2);
}

.navlink {
    color: #fff;
    text-decoration: none;
    font-size: calc(5px + 3vh);
}


.innernavlink {
    padding: 1% 0px 1% 10%;
}

